@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap);
:root{--clr-primary: #2c1c14;--clr-secondary: #99cc00;--clr-highlight: #f15639;--clr-background: #fdf8f2;--clr-nav: #e0d6ca;--main-font: 'Montserrat', sans-serif;--header-font: 'Montserrat', sans-serif;--nav-font: 'Montserrat', sans-serif;--error: #ff4a4a;--main-radius: 5px;--main-padding: 5px}*{box-sizing:border-box;margin:0;padding:0;font-family:'Montserrat', sans-serif;font-family:var(--main-font)}html,body{background:#fdf8f2;background:var(--clr-background);overflow:visible}h1,h2,h3,h4,h5,h6{font-family:'Montserrat', sans-serif;font-family:var(--header-font)}.row{display:flex;flex-direction:row;flex-wrap:wrap;width:100%}.column{display:flex;flex-direction:column;flex-basis:100%;flex:1 1}@media screen and (max-width: 600px){.row{flex-direction:column;align-items:center}}.gallery-container{justify-content:center;align-items:center;text-align:center;padding:25px}.progress-bar{height:5px;background:#f15639;background:var(--clr-highlight);margin-top:20px}.gallery-label input{height:0;width:0;opacity:0}.gallery-label{display:block;width:30px;height:30px;border:1px solid #f15639;border:1px solid var(--clr-highlight);border-radius:50%;margin:10px auto;line-height:30px;color:#f15639;color:var(--clr-highlight);font-weight:bold;font-size:24px}gallery-label:hover{background:#f15639;background:var(--clr-highlight);color:white}.output{height:60px;font-size:0.8rem}.error{color:#ff4a4a;color:var(--error)}.img-grid{margin:20px auto;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:40px}@media screen and (max-width: 480px){.img-grid{grid-template-columns:1fr}}.img-wrap{overflow:hidden;height:0;padding:50% 0;position:relative;opacity:0.8;border-radius:10px;box-shadow:0 5px 10px 0 rgba(0,0,0,0.2),0 7px 20px 0 rgba(0,0,0,0.2)}.img-wrap:hover{cursor:pointer}.img-wrap img{min-width:100%;min-height:100%;max-width:150%;position:absolute;top:0;left:0}.backdrop{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.5)}.backdrop img{display:block;max-width:60%;max-height:80%;margin:60px auto;box-shadow:3px 5px 7px rgba(0,0,0,0.5);border:3px solid white}

.form-container{margin:100px auto;width:80%;box-shadow:0 5px 8px 0 rgba(0,0,0,0.2),0 7px 20px 0 rgba(0,0,0,0.2);position:relative;border-radius:10px;display:grid;grid-template-columns:1fr;grid-template-rows:0.2fr 1fr;grid-template-areas:'signup-header' 'signup-main'}.form-header{background:linear-gradient(90deg, #9c0 0%, #ade40a 100%);position:relative;grid-area:signup-header;border-radius:10px 10px 0 0;justify-content:center;align-items:center}.form-header>h1{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:100%;text-align:center;font-size:2rem;color:var(--clr-primary)}.form-img{width:80%;height:50%;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.form-success-img{width:60%;height:60%;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.form-success{text-align:center;font-size:24px;margin-top:80px;color:#fff}.form-content{position:relative;background:linear-gradient(90deg, #2c1c14 0%, #332017 100%);grid-area:signup-main;border-radius:0 0 10px 10px}.form-content-right{position:relative;background:linear-gradient(90deg, #2c1c14 0%, #332017 100%);grid-area:signup-main;border-radius:0 0 10px 10px}.form{position:relative;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:10px}.form h1{font-size:1rem;text-align:start;width:80%;margin-bottom:1rem;color:#fff}.form-inputs{margin:0.5rem;width:80%}.form-inputs p{font-size:0.8rem;margin-top:0.5rem;color:#f00e0e}.form-label{display:inline-block;font-size:0.8rem;margin-bottom:6px;color:#fff}.form-input{display:block;padding-left:10px;outline:none;border-radius:2px;height:40px;width:100%;border:none}.form-input::-webkit-input-placeholder{color:#595959;font-size:12px}.form-input:-ms-input-placeholder{color:#595959;font-size:12px}.form-input::placeholder{color:#595959;font-size:12px}.form-input-btn{width:250px;height:50px;margin-top:25px;border-radius:2px;background:linear-gradient(90deg, #e04e1e 0%, #f35e2c 100%);outline:none;border:none;color:#fff;font-size:1rem}.form-input-btn:hover{cursor:pointer;background:linear-gradient(90deg, #f35e2c 0%, #f86636 100%);transition:all 0.2s ease-in-out}.form-input-login{font-size:0.8rem;margin-top:10px;color:#fff;width:80%;text-align:center}.form-input-login a{text-decoration:none;color:#27cdff;font-weight:600}.address-input{flex:4 1}@media screen and (max-width: 480px){.form-container{margin:0 auto;width:100%;height:100%;box-shadow:none;grid-template-rows:unset}.form-header{height:100px;width:100%;border-radius:0}.form-content-right{display:flex;flex-direction:column;flex-basis:100%;flex:1 1;height:100%;border-radius:0px}.form-content{display:flex;flex-direction:column;flex-basis:100%;flex:1 1;height:100%;border-radius:0px}.form{position:relative;top:0;left:0;width:100%;transform:none;padding:50px 0px}}

